.goals {
	margin: 0 auto;
	max-width: 25rem; }


.goal {
	max-width: 25rem;
	font-size: 1.2rem;
	margin-bottom: 2.5rem;
	display: block;

	.goal-info {
		@media ( min-width: 20rem ) {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%; } }

	h2, h3, .numbers {
		font-size: 1.2rem;
		font-weight: 300;
		position: static;
		margin: 0.5rem 0;
		top: 0; }

	h2 {
		color: $grey50;
		left: 0;
		font-family: 'Roboto Condensed', sans-serif;
		transition: color 200ms ease; }

	&:hover h2 {
		color: #000; }

	h3 {
		color: $grey50;
		font-size: 1rem;
		right: 0;
		font-family: 'Roboto Condensed', sans-serif;

		mark {
			color: black;
			background: transparent;
			font-weight: 700;
			font-size: 1.2rem;
			font-family: 'Roboto', sans-serif; } }

	.target {
		font-weight: 700;
		display: inline; }

	.divider {
		color: $grey20; }

	progress[value] {
		/* Reset the default appearance */
		-webkit-appearance: none;
		appearance: none;

		width: 100%;
		height: 3px;
		@media(min-width: 20rem) {
			margin: 2rem 0 0; } }

	progress[value]::-webkit-progress-bar {
		background-color: $grey10; }

	progress[value]::-webkit-progress-value {
		background: $red; }


	.edit {
		max-height: 0;
		overflow: hidden;
		input[type="number"] {
			margin-left: 0.25rem;
			width: 5rem; } }

	&.editor-open .edit {
		max-height: 7rem; } }


@media (min-width: 20rem) {
	.goal {
		padding-top: 2.5rem;

		h2, h3, .numbers {
			position: absolute;
			margin: 0; }

		.numbers {
			right: 50%;
			margin-right: -1.75rem; } } }




.set-goals {
	max-height: 0;
	position: absolute;
	width: 90%;
	top: 0;
	left: 5%;
	background: #fff;
	padding: 0 10% 0;
	overflow: hidden;
	transition: all 200ms ease;
	opacity: 0;
	top: -10rem;

	.show-set-goals & {
		top: 1rem;
		padding: 1rem 10% 3rem;
		max-height: 100rem;
		opacity: 1; }


	fieldset {
		padding: 0; }

	label {
		font-weight: 500;
		font-size: 1.1rem;
		width: 4.5rem;
		display: inline-block; }

	input {
		width: 5rem; } }

#set-goals {
	margin-top: 1rem;
	padding: 0.5rem 2.5rem;
	width: auto; }

#skip-goals {}



.show-set-goals {
	&:before {
		content: " ";
		position: absolute;
		display: block;
		width: 100vw;
		height: 100vh;
		background: rgba(#000, 0.25);
		top: 0;
		left: 0; } }
